import React from 'react';
import { Keypad } from '@components/calculator/keypad/keypadCC';
import { LeftSide, Calculator, RightSide } from '@components/calculator';
import { Display } from '@components/calculator/display/displayCC';
import { History } from '@components/calculator/history/historyCC';

export class CalculatorCC extends React.Component {
  render() {
    return (
      <Calculator>
        <LeftSide>
          <Display/>
          <Keypad/>
        </LeftSide>
        <RightSide>
          <History/>
        </RightSide>
      </Calculator>
    )
  }
}
