import React from 'react';
import { DisplayHistory, DisplayMain } from '@components/calculator/display/styles';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

class DisplayComponent extends React.Component {
  render() {
    return (
      <React.Fragment>
        <DisplayHistory>{this.props.expression}</DisplayHistory>
        <DisplayMain>{this.props.currentValue || this.props.result}</DisplayMain>
      </React.Fragment>
    )
  }
}

const mapStateToProps = state => ({
  currentValue: state.app.currentValue,
  expression: state.app.expression,
  result: state.app.result,
})

export const Display = connect(mapStateToProps)(DisplayComponent);

DisplayComponent.propTypes = {
  expression: PropTypes.string,
  currentValue: PropTypes.string,
  result: PropTypes.string,
}
