import React from 'react';
import { DisplayHistory, DisplayMain } from '@components/calculator/display/styles';
import { useSelector } from 'react-redux';
import { selectCurrentValue, selectExpression, selectResult } from '@store/selectors';

export const Display = () => {
  const expression = useSelector(selectExpression);
  const currentValue = useSelector(selectCurrentValue);
  const result = useSelector(selectResult);

  return (
    <React.Fragment>
      <DisplayHistory>{expression}</DisplayHistory>
      <DisplayMain>{currentValue || result}</DisplayMain>
    </React.Fragment>
  )
}
