import React from 'react';
import { KeypadContainer, Button } from '@components/calculator/keypad/styles';
import { operations } from '@constants/operations';
import { useDispatch, useSelector } from 'react-redux';
import { selectCurrentValue, selectOperation, selectExpression } from '@store/selectors';
import { keypadHandler } from '@helpers';

export const Keypad = () => {
  const dispatch = useDispatch();

  const expression = useSelector(selectExpression);
  const currentValue = useSelector(selectCurrentValue);
  const operation = useSelector(selectOperation);

  const handleClick = event => {
    keypadHandler(event, currentValue, expression, operation, dispatch);
  }

  return (
    <KeypadContainer>
      {operations.map(({ id, value }) => <Button key={id} onClick={handleClick}>{value}</Button>)}
    </KeypadContainer>
  )
}
